import $ from "jquery";
import Chart from "chart.js";

export function renderTo(target) {
    $.get("/api/visualizations")
    .done(function(res) {
        $("#" + target).html("Request response: " + JSON.stringify(res));
    })
    .fail(function(err) {
        console.log("error: ", err);
    });
};

export function renderChart(target) {
    var canvas = $("<canvas>");

    var context = canvas.get(0).getContext("2d");
    var myChart = new Chart(context, {
        type: 'bar',
        data: {
            labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
            datasets: [{
                label: 'Chart Example',
                data: [12, 19, 3, 5, 2, 3],
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)'
                ],
                borderColor: [
                    'rgba(255,99,132,1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)'
                ],
                borderWidth: 1
            }]
        },
        options: {
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero:true
                    }
                }]
            }
        }
    });

    canvas.appendTo(document.body)
}



